import React, { Suspense } from "react";
import { TileLayer, Marker } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import useIntersectionObserver from "../../scroll/useIntersectionObserver";
import "./AgenceLocation.css";

const MapContainer = React.lazy(() =>
  import("react-leaflet").then((module) => ({ default: module.MapContainer }))
);

const AgenceLocation = () => {
  const position = [48.8706298, 2.3169336];

  const [targetRef, isIntersecting] = useIntersectionObserver({
    threshold: [.5, .75], 
  });

  return (
    <section
      className={`flexCenter map-ctn ${isIntersecting ? "show" : "hidden"}`}
      ref={targetRef}
    >
      <Suspense fallback={<p>LOADING...</p>}>
        <MapContainer center={position} zoom={13} style={{ height: "400px" }}>
          <TileLayer
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright" alt="learn more about openstreetmap">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          <Marker position={position} />
        </MapContainer>

        <p>
          Notre siège social se situe à Paris. N'hésitez pas à venir nous
          rencontrer !
        </p>
      </Suspense>
    </section>
  );
};

export default AgenceLocation;
